.new-expense {
  background-color: #a892ee;
  padding: 16px;
  margin: 32px auto;
  width: 800px;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}
button {
  font: inherit;
  cursor: pointer;
  padding: 16px 32px;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: white;
  border-radius: 12px;
  margin-right: 16px;
}
button:hover,
button:active {
  background-color: #510674;
  border-color: #510674;
}
